e, and also import other style files */\r\n\r\n/* SCSS */\r\n@import \"scss/style\";\r\n.pcoded-navbar .pcoded-inner-navbar li > a {\r\n  padding: 22px 10px !important;\r\n  padding-left: 35px !important;\r\n  font-size: 13px !important;\r\n}\r\n\r\n.pcoded-navbar.navbar-collapsed .pcoded-inner-navbar li > a {\r\n  padding-left: 0px !important;\r\n  padding: 20px 10px !important;\r\n}\r\n\r\n.pcoded-navbar .pcoded-inner-navbar li > a > .pcoded-micon {\r\n  position: absolute;\r\n  left: 0;\r\n  top: 50%;\r\n  transform: translateY(-50%);\r\n}\r\n\r\n.pcoded-navbar.navbar-collapsed .pcoded-inner-navbar li > a > .pcoded-micon {\r\n  position: relative !important;\r\n  left: 0;\r\n  top: 50%;\r\n  transform: translateY(-50%);\r\n}\r\n\r\n.pcoded-navbar.menu-light .pcoded-inner-navbar app-nav-item > li.nav-item.active > a, .pcoded-navbar.menu-light .pcoded-inner-navbar app-nav-item > li.nav-item.pcoded-trigger > a {\r\n  color: #4680ff !important;\r\n  background: transparent !important;\r\n}\r\n\r\n.pcoded-inner-navbar .ng-star-inserted:not(:last-child) > li::after {\r\n  content: \"\";\r\n  position: absolute;\r\n  height: 39px;\r\n  top: 46px;\r\n  background-color: #c7c4c4;\r\n  width: 1px;\r\n  left: 25px;\r\n}\r\n\r\n.pcoded-navbar.navbar-collapsed .pcoded-inner-navbar .ng-star-inserted:not(:last-child) > li::after {\r\n  content: \"\";\r\n  position: absolute;\r\n  height: 39px;\r\n  top: 39px;\r\n  background-color: #c7c4c4;\r\n  width: 1px;\r\n  left: 35px;\r\n}\r\n\r\n.card-active::after {\r\n  content: \"\";\r\n  position: absolute;\r\n  width: 30px;\r\n  height: 30px;\r\n  background: #fff;\r\n  top: 50%;\r\n  right: -15px;\r\n  box-shadow: -1px -1px 10px -9px rgba(0, 0, 0, 0.5);\r\n  transform: translateY(-50%) rotate(135deg);\r\n}","/**======================================================================\r\n=========================================================================\r\nTemplate Name: Able Pro Admin Template\r\nAuthor: Codedthemes\r\nFile: st