,\r\n//                 &:before {\r\n//                     cursor: not-allowed;\r\n//                 }\r\n//             }\r\n//         }\r\n//     }\r\n//\r\n//     &.radio-fill {\r\n//         input[type=radio] {\r\n//             + .cr {\r\n//                 &:after {\r\n//                     width: 18px;\r\n//                     height: 18px;\r\n//                     top: 9px;\r\n//                     left: 2px;\r\n//                     @supports (-webkit-overflow-scrolling: touch) {\r\n//                         top: 10px;\r\n//                     }\r\n//                     @media (-ms-high-contrast: none), (-ms-high-contrast: active) {\r\n//                         top: 2px;\r\n//                     }\r\n//                 }\r\n//             }\r\n//         }\r\n//     }\r\n//     @each $value in $color-bt-name {\r\n//         $i: index($color-bt-name, $value);\r\n//\r\n//         &.radio-#{$value} input[type=radio]:checked + .cr {\r\n//             &:before {\r\n//                 border-color: nth($color-bt-color,$i);\r\n//             }\r\n//\r\n//             &:after {\r\n//                 background: nth($color-bt-color,$i);\r\n//             }\r\n//         }\r\n//     }\r\n//\r\n//     .cr {\r\n//         cursor: pointer;\r\n//     }\r\n// }\r\n// @-moz-document url-prefix() {\r\n//     .radio input[type=\"radio\"] + .cr::after {\r\n//         top: 12px;\r\n//     }\r\n// }\r\n//\r\n// .form-group.d-inline {\r\n//     .radio {\r\n//         input[type=radio] {\r\n//             + .cr {\r\n//                 &:after {\r\n//                     @media (-ms-high-contrast: none), (-ms-high-contrast: active) {\r\n//                         top: 6px;\r\n//                     }\r\n//                 }\r\n//             }\r\n//         }\r\n//\r\n//         &.radio-fill {\r\n//             input[type=radio] {\r\n//                 + .cr {\r\n//                     &:after {\r\n//                         @media (-ms-high-contrast: none), (-ms-high-contrast: active) {\r\n//                             top: 3px;\r\n//                         }\r\n//                     }\r\n//                 }\r\n//             }\r\n//         }\r\n//     }\r\n// }\r\n//\r\n// .custom-controls-stacked {\r\n//     .radio {\r\n//         input[type=radio] {\r\n//             + .cr {\r\n//                 &:after {\r\n//                     top: 15px;\r\n//                 }\r\n//             }\r\n//         }\r\n//     }\r\n// }\r\n/**====== Radio & Checked css end ======**/\r\n","/**  =====================\n      Switches css start\n==========================  **/\r\n\r\n.switch {\r\n    input[type=checkbox] {\r\n        opacity: 0;\r\n        position: absolute;\r\n\r\n        + .cr {\r\n            position: relative;\r\n            display: inline-block;\r\n            transition: 0.4s ease;\r\n            height: 20px;\r\n            width: 35px;\r\n            border: 1px solid #e9eaec;\r\n            border-radius: 60px;\r\n            cursor: pointer;\r\n            z-index: 0;\r\n            top: 12px;\r\n\r\n            &:after,\r\n            &:before {\r\n                content: \"\";\r\n                position: absolute;\r\n                display: block;\r\n                top: 0;\r\n                left: 0;\r\n            }\r\n\r\n            &:before {\r\n                transition: 0.2s cubic-bezier(0.24, 0, 0.5, 1);\r\n                height: 20px;\r\n                width: 35px;\r\n                border-radius: 30px;\r\n            }\r\n\r\n            &:after {\r\n                box-shadow: 0 0 0 1px rgba(0, 0, 0, 0.1), 0 4px 0 0 rgba(0, 0, 0, 0.04), 0 4px 9px rgba(0, 0, 0, 0.13), 0 3px 3px rgba(0, 0, 0, 0.05);\r\n                transition: 0.35s cubic-bezier(0.54, 1.6, 0.5, 1);\r\n                background: #f7f7f7;\r\n                height: 19px;\r\n                width: 19px;\r\n                border-radius: 60px;\r\n            }\r\n        }\r\n\r\n        &:checked + .cr {\r\n            &:before {\r\n                background: $primary-color;\r\n                transition: width 0.2s cubic-bezier(0, 0, 0, 0.1);\r\n            }\r\n\r\n            &:after {\r\n                left: 16px;\r\n            }\r\n        }\r\n\r\n        &:disabled + label {\r\n            opacity: 0.5;\r\n            filter: grayscale(0.4);\r\n            cursor: not-allowed;\r\n        }\r\n    }\r\n    @each $value in $color-bt-name {\r\n        $i: index($color-bt-name, $value);\r\n\r\n        &.switch-#{$value} input[type=checkbox]:checked + .cr:before {\r\n            background: nth($color-bt-color,$i);\r\n        }\r\n    }\r\n}\r\n/**====== Switches css end ======**/\r\n","/**  =====================\n      Badges css start\n==========================  **/\r\n\r\n.badge {\r\n    font-size: 80%;\r\n    padding: 0.35em 0.6em;\r\n    font-weight: 600;\r\n}\r\n@each $color, $value in $theme-colors {\r\n    .badge-light-#{$color} {\r\n        background: transparentize($value,.8);\r\n        border-color: theme-color-level($color, $alert-border-level);\r\n        color: $value;\r\n    }\r\n\r\n    a.badge-light-#{$color} {\r\n        &.active,\r\n        &:active,\r\n        &:focus,\r\n        &:hover {\r\n            background: $value;\r\n            border-color: $value;\r\n            color: theme-color-level($color, $alert-bg-level);\r\n        }\r\n    }\r\n}\r\n/**====== Badges css end ======**/\r\n","/**  =====================\n      Custom css start\n==========================  **/\r\nbody {\r\n    font-family: $theme-font-family;\r\n    font-size: $theme-font-size;\r\n    color: $theme-font-color;\r\n    font-weight: 400;\r\n    // background: $theme-background;\r\n    background: $primary-color;\r\n    position: relative;\r\n}\r\n\r\n* {\r\n    &:focus {\r\n        outline: none;\r\n    }\r\n}\r\n\r\na {\r\n    &:hover {\r\n        outline: none;\r\n        text-decoration: none;\r\n    }\r\n}\r\n\r\nh1,\r\nh2,\r\nh3,\r\nh4,\r\nh5,\r\nh6 {\r\n    color: $theme-heading-color;\r\n    font-weight: 600;\r\n}\r\n\r\nh1 {\r\n    font-size: 2.5rem;\r\n}\r\n\r\nh2 {\r\n    font-size: 2rem;\r\n}\r\n\r\nh3 {\r\n    font-size: 1.75rem;\r\n}\r\n\r\nh4 {\r\n    font-size: 1.5rem;\r\n}\r\n\r\nh5 {\r\n    font-size: 1rem;\r\n}\r\n\r\nh6 {\r\n    font-size: 0.875rem;\r\n}\r\n\r\np {\r\n    font-size: 13px;\r\n}\r\n\r\nb,\r\nstrong {\r\n    font-weight: 600;\r\n}\r\n\r\n.img-radius {\r\n    border-radius: 50%;\r\n}\r\n\r\n.page-header {\r\n    background: transparent;\r\n    margin-top: -140px;\r\n    margin-bottom: 30px;\r\n\r\n    h5 {\r\n        margin-bottom: 15px;\r\n        font-weight: 400;\r\n        color: #fff;\r\n    }\r\n\r\n    .page-header-title + .breadcrumb {\r\n        background: transparent;\r\n        padding: 0;\r\n        margin-bottom: 0;\r\n\r\n        > .breadcrumb-item {\r\n            line-height: 1;\r\n            &:before{\r\n                color: rgba(255,255,255,0.5);\r\n            }\r\n            a {\r\n                color: #fff;\r\n                font: {\r\n                    weight: 400;\r\n                }\r\n            }\r\n\r\n            &:last-child a {\r\n                color: #fff;\r\n                font-weight: 600;\r\n            }\r\n        }\r\n    }\r\n\r\n    &:not(.breadcumb-sticky) {\r\n        .page-header-title + .breadcrumb {\r\n            > .breadcrumb-item {\r\n                a {\r\n                    font: {\r\n                        size: 13px;\r\n                    }\r\n                }\r\n            }\r\n        }\r\n    }\r\n\r\n    &.breadcumb-sticky {\r\n        // padding: 15px 25px;\r\n        // margin-top: -25px;\r\n        // margin-left: -25px;\r\n        // margin-right: -25px;\r\n        // box-shadow: 0 0 3px 0 rgba(54, 80, 138, 0.3);\r\n        overflow: hidden;\r\n        position: relative;\r\n\r\n\r\n        .page-header-title {\r\n            display: inline-block;\r\n\r\n            h5 {\r\n                margin-bottom: 0;\r\n                color: #fff;\r\n            }\r\n        }\r\n        .breadcrumb-item + .breadcrumb-item::before,\r\n        .page-header-title + .breadcrumb > .breadcrumb-item a,\r\n        .page-header-title + .breadcrumb > .breadcrumb-item:last-child a{\r\n            color: #fff;\r\n        }\r\n\r\n        .breadcrumb {\r\n            display: inline-flex;\r\n            float: right;\r\n            margin-top: 4px;\r\n            @media only screen and (max-width: 767px) {\r\n                display: flex;\r\n                float: none;\r\n            }\r\n        }\r\n    }\r\n}\r\n\r\n.pcoded-main-container {\r\n    .page-header {\r\n        +.row{\r\n            // margin-top: -35px;\r\n        }\r\n    }\r\n}\r\n/* ==========  card css start  =========== */\r\n\r\n.anim-rotate {\r\n    animation: anim-rotate 1s linear infinite;\r\n}\r\n@keyframes anim-rotate {\r\n    100% {\r\n        transform: rotate(360deg);\r\n    }\r\n}\r\n\r\n.anim-close-card {\r\n    animation: anim-close-card 1.4s linear;\r\n}\r\n@keyframes anim-close-card {\r\n    100% {\r\n        opacity: 0.3;\r\n        transform: scale3d(.3, .3, .3);\r\n    }\r\n}\r\n/* ==========  scrollbar End  =========== */\r\n\r\n.scroll-y {\r\n    z-index: 1027;\r\n}\r\n/* ==========  scrollbar End  ===========\n ==========  card css End  ===========\n ================================    Dropdown Start  ===================== */\r\n\r\n.dropdown-menu {\r\n    padding: 10px 0;\r\n    // box-shadow: 0 0 4px 0 rgba(43, 43, 43, 0.25);\r\n    // border: none;\r\n    font-size: $theme-font-size;\r\n\r\n    > li,\r\n    > .dropdown-item {\r\n        padding-left: 8px;\r\n        padding-right: 8px;\r\n        margin: 0 10px;\r\n        width: auto;\r\n        border-radius: 2px;\r\n\r\n        > a {\r\n            padding: 5px;\r\n            color: $theme-font-color;\r\n\r\n            i {\r\n                font-size: 15px;\r\n                display: inline-flex;\r\n                align-items: center;\r\n                justify-content: center;\r\n                margin-right: 10px;\r\n            }\r\n        }\r\n\r\n        &.active,\r\n        &:active,\r\n        &:focus,\r\n        &:hover {\r\n            background: $primary-color;\r\n            color: #fff;\r\n            box-shadow: 0 0 4px 0 rgba(43, 43, 43, 0.25);\r\n\r\n            > a {\r\n                background: transparent;\r\n                color: #fff;\r\n            }\r\n        }\r\n    }\r\n\r\n    > a {\r\n        &.active,\r\n        &:active,\r\n        &:focus,\r\n        &:hover {\r\n            background: $primary-color;\r\n            color: #fff;\r\n        }\r\n    }\r\n}\r\n\r\n.drp-icon {\r\n    &:after {\r\n        display: none;\r\n    }\r\n}\r\n/* ================================    Dropdown End  ===================== */\r\n@media (min-width: 1600px) {\r\n    .container {\r\n        max-width: 1540px;\r\n    }\r\n}\r\n@media (max-width: 992px) {\r\n    .container {\r\n        max-width: 100%;\r\n    }\r\n}\r\n/* ========================================================\n ===============     document      ======================\n ========================================================\n\n Grid examples\n*/\r\n\r\n.bd-example-row {\r\n    background: $theme-background;\r\n    padding: 15px;\r\n    margin-bottom: 15px;\r\n\r\n    .row {\r\n        > [class^=\"col-\"],\r\n         > .col {\r\n            padding-top: 0.75rem;\r\n            padding-bottom: 0.75rem;\r\n            background-color: rgba(86, 61, 124, .15);\r\n            border: 1px solid rgba(86, 61, 124, .2);\r\n        }\r\n    }\r\n\r\n    .row + .row {\r\n        margin-top: 1rem;\r\n    }\r\n\r\n    .flex-items-bottom,\r\n    .flex-items-middle,\r\n    .flex-items-top {\r\n        min-height: 6rem;\r\n        background-color: rgba(255, 0, 0, .1);\r\n    }\r\n}\r\n\r\n.bd-example-row-flex-cols .row {\r\n    min-height: 10rem;\r\n    background-color: rgba(255, 0, 0, .1);\r\n}\r\n/* Docs examples */\r\n\r\n.bd-example {\r\n    position: relative;\r\n    padding: 1rem;\r\n    margin: 1rem;\r\n    border: solid #ddd;\r\n    border-width: 0.2rem 0 0;\r\n    @media only screen and (max-height: 575px) {\r\n        padding: 1.5rem;\r\n        margin-right: 0;\r\n        margin-left: 0;\r\n        border-width: 0.2rem;\r\n    }\r\n}\r\n/* Example modals */\r\n\r\n.modal {\r\n    z-index: 1072;\r\n\r\n    .popover,\r\n    .tooltip {\r\n        z-index: 1073;\r\n    }\r\n}\r\n\r\n.modal-backdrop {\r\n    z-index: 1071;\r\n}\r\n\r\n.bd-example-modal {\r\n    background-color: #fafafa;\r\n\r\n    .modal {\r\n        position: relative;\r\n        top: auto;\r\n        right: auto;\r\n        bottom: auto;\r\n        left: auto;\r\n        z-index: 1;\r\n        display: block;\r\n    }\r\n\r\n    .modal-dialog {\r\n        left: auto;\r\n        margin-right: auto;\r\n        margin-left: auto;\r\n    }\r\n}\r\n/* Example tabbable tabs */\r\n\r\n.bd-example-tabs .nav-tabs {\r\n    margin-bottom: 1rem;\r\n}\r\n/* Code snippets */\r\n\r\n.highlight {\r\n    padding: 1rem;\r\n    margin-top: 1rem;\r\n    margin-bottom: 1rem;\r\n    background-color: #f3f2fb;\r\n    -ms-overflow-style: -ms-autohiding-scrollbar;\r\n    @media only screen and (max-height: 575px) {\r\n        padding: 1.5rem;\r\n    }\r\n}\r\n\r\n.bd-content .highlight {\r\n    margin-right: 15px;\r\n    margin-left: 15px;\r\n    @media only screen and (max-height: 575px) {\r\n        margin-right: 0;\r\n        margin-left: 0;\r\n    }\r\n}\r\n\r\n.highlight {\r\n    pre {\r\n        padding: 0;\r\n        margin-top: 0;\r\n        margin-bottom: 0;\r\n        background-color: transparent;\r\n        border: 0;\r\n    }\r\n\r\n    pre code {\r\n        font-size: inherit;\r\n        color: #333;\r\n    }\r\n}\r\n\r\n:not(pre) > code[class*=\"language-\"],\r\npre[class*=\"language-\"] {\r\n    background: transparent;\r\n}\r\n/* =======================================================================\n ===============     Ui kit copy model style      ====================== */\r\n/* Code examples */\r\n\r\n.datta-example {\r\n    position: relative;\r\n\r\n    .datta-example-btns {\r\n        position: absolute;\r\n        right: 30px;\r\n        top: -10px;\r\n        opacity: 0;\r\n        transform: rotateX(-90deg);\r\n        transition: all 0.3s ease-in-out;\r\n\r\n        .datta-example-btn {\r\n            display: inline-block;\r\n            line-height: 1;\r\n            font-weight: 600;\r\n            background: $primary-color;\r\n            color: #fff;\r\n            padding: 0.1875rem 0.3125rem;\r\n            border-radius: 2px;\r\n            white-space: nowrap;\r\n            font-size: 11px;\r\n            margin: 0 4px;\r\n\r\n            &.copy::before {\r\n                content: \"COPY\";\r\n            }\r\n\r\n            &.copied {\r\n                background: $success-color !important;\r\n                color: #fff !important;\r\n\r\n                &::before {\r\n                    content: \"COPIED!\";\r\n                }\r\n            }\r\n        }\r\n    }\r\n\r\n    &:hover .datta-example-btns {\r\n        top: -21px;\r\n        transform: rotateX(0deg);\r\n        opacity: 1;\r\n    }\r\n}\r\n/* Modal */\r\n\r\n.datta-example-modal {\r\n    position: fixed;\r\n    z-index: 1099;\r\n    top: 0;\r\n    right: 0;\r\n    width: 100%;\r\n    height: 100%;\r\n    overflow: hidden;\r\n    background: #282c34;\r\n    transform-origin: 50% 0;\r\n    transform: scale(0.0);\r\n    transition: all 0.3s ease-in-out;\r\n}\r\n\r\n.datta-example-modal-opened {\r\n    overflow: hidden !important;\r\n\r\n    .datta-example-modal {\r\n        transform: scale(1);\r\n    }\r\n}\r\n\r\n.datta-example-modal-content {\r\n    max-width: 100vw;\r\n    margin: auto;\r\n    padding: 50px;\r\n    height: 100vh;\r\n    overflow: auto;\r\n\r\n    > pre {\r\n        overflow: hidden;\r\n        width: fit-content;\r\n\r\n        > code {\r\n            padding: 0;\r\n            background: none;\r\n            font-size: 16px;\r\n        }\r\n    }\r\n}\r\n\r\n.md-datta-example-modal-copy {\r\n    display: block;\r\n    position: fixed;\r\n    top: 90px;\r\n    right: 30px;\r\n    margin-right: 8px;\r\n    font-size: 1.5rem;\r\n    border-radius: 50%;\r\n    padding: 10px 15px;\r\n    background: $primary-color;\r\n    color: #fff;\r\n    box-shadow: 0 10px 20px 0 rgba(0,0,0,0.2);\r\n    z-index: 1;\r\n    white-space: nowrap;\r\n\r\n    &:hover {\r\n        box-shadow: 0 10px 20px 0 rgba(0,0,0,0.2);\r\n        color: #fff;\r\n    }\r\n\r\n    &.copied::before {\r\n        content: \"Copied to Clipboard Successfully ! . . .\";\r\n        position: absolute;\r\n        display: block;\r\n        right: 100%;\r\n        margin-right: 10px;\r\n        font-size: 14px;\r\n        background: $success-color;\r\n        line-height: 24px;\r\n        height: 24px;\r\n        border-radius: 3px;\r\n        padding: 0 6px;\r\n        top: 50%;\r\n        margin-top: -12px;\r\n    }\r\n}\r\n\r\n.datta-example-modal-close {\r\n    display: block;\r\n    position: fixed;\r\n    top: 10px;\r\n    right: 52px;\r\n    color: #fff;\r\n    opacity: 0.2;\r\n    font-size: 3rem;\r\n    font-weight: 100;\r\n    transition: all 0.3s ease-in-out;\r\n    z-index: 1;\r\n\r\n    &:hover {\r\n        color: #fff;\r\n        opacity: 0.9;\r\n    }\r\n}\r\n/* Code */\r\n.cui-bottom-spacer {\r\n    height: 12rem;\r\n}\r\n/* editor style for model */\r\n.hljs {\r\n    padding: 0.5em;\r\n    color: #abb2bf;\r\n    background: #282c34;\r\n}\r\n\r\n.hljs-comment,\r\n.hljs-quote {\r\n    color: #5c6370;\r\n    font-style: italic;\r\n}\r\n\r\n.hljs-doctag,\r\n.hljs-formula,\r\n.hljs-keyword {\r\n    color: #c678dd;\r\n}\r\n\r\n.hljs-deletion,\r\n.hljs-name,\r\n.hljs-section,\r\n.hljs-selector-tag,\r\n.hljs-subst {\r\n    color: #e06c75;\r\n}\r\n\r\n.hljs-literal {\r\n    color: #56b6c2;\r\n}\r\n\r\n.hljs-addition,\r\n.hljs-attribute,\r\n.hljs-meta-string,\r\n.hljs-regexp,\r\n.hljs-