\r\n    text-align: center;\r\n    color: #fff;\r\n    font-family: \"Courier New\", Courier, monospace;\r\n    padding: 50px 0;\r\n\r\n    p {\r\n        font-size: 17px;\r\n    }\r\n\r\n    h1 {\r\n        color: #fff;\r\n    }\r\n\r\n    .iew-container {\r\n        min-width: 1024px;\r\n        width: 100%;\r\n        height: 200px;\r\n        background: #fff;\r\n        margin: 50px 0;\r\n    }\r\n\r\n    .iew-download {\r\n        list-style: none;\r\n        padding: 30px 0;\r\n        margin: 0 auto;\r\n        width: 720px;\r\n\r\n        > li {\r\n            float: left;\r\n            vertical-align: top;\r\n\r\n            > a {\r\n                display: block;\r\n                color: #000;\r\n                width: 140px;\r\n                font-size: 15px;\r\n                padding: 15px 0;\r\n\r\n                > div {\r\n                    margin-top: 10px;\r\n                }\r\n\r\n                &:hover {\r\n                    background-color: #eee;\r\n                }\r\n            }\r\n        }\r\n    }\r\n}\r\n/* ================================    browser  warning  End  ===================== */\r\ndiv.code-toolbar {\r\n    > .toolbar a,\r\n     > .toolbar span,\r\n    > .toolbar button {\r\n        padding: 3px 0.9em !important;\r\n        background: #04a9f5 !important;\r\n        color: #fff !important;\r\n        box-shadow: none !important;\r\n    }\r\n}\r\n\r\npre[class*=language-]:after,\r\npre[class*=language-]:before {\r\n    display: none;\r\n}\r\n/* table css */\r\n\r\n.table-borderless tbody tr td,\r\n.table-borderless tbody tr th {\r\n    border: 0;\r\n}\r\n@media only screen and (max-width: 400px) {\r\n    .lay-customizer .theme-color > a[data-value=\"reset\"],\r\n    .theme-color > a[data-value=\"reset\"] {\r\n        margin-top: 20px;\r\n    }\r\n}\r\n","/**  =====================\r\n      Generic-class css start\r\n========================== **/\r\n/*====== Padding , Margin css starts ======*/\r\n$i: 0;\r\n@while $i<=50 {\r\n    .p {\r\n        &-#{$i} {\r\n            padding: #{$i}px;\r\n        }\r\n\r\n        &-t-#{$i} {\r\n            padding-top: #{$i}px;\r\n        }\r\n\r\n        &-b-#{$i} {\r\n            padding-bottom: #{$i}px;\r\n        }\r\n\r\n        &-l-#{$i}{\r\n            padding-left: #{$i}px;\r\n        }\r\n\r\n        &-r-#{$i} {\r\n            padding-right: #{$i}px;\r\n        }\r\n    }\r\n    .m {\r\n        &-#{$i} {\r\n            margin: #{$i}px;\r\n        }\r\n\r\n        &-t-#{$i} {\r\n            margin-top: #{$i}px;\r\n        }\r\n\r\n        &-b-#{$i} {\r\n            margin-bottom: #{$i}px;\r\n        }\r\n\r\n        &-l-#{$i} {\r\n            margin-left: #{$i}px;\r\n        }\r\n\r\n        &-r-#{$i} {\r\n            margin-right: #{$i}px;\r\n        }\r\n    }\r\n    $i: $i+5;\r\n}\r\n/*====== Padding , Margin css ends ======*/\r\n\r\n/*====== text-color, background & border css starts ======*/\r\n@each $value in $color-name {\r\n    $i: index($color-name, $value);\r\n    .bg-#{$value} {\r\n        background-color: nth($color-color,$i),;\r\n    }\r\n    .text-#{$value} {\r\n        color: nth($color-color,$i);\r\n    }\r\n    .b-#{$value} {\r\n        border: 1px solid nth($color-color,$i);\r\n    }\r\n}\r\n/*====== text-color, background & border css ends ======*/\r\n/*====== [ overrides bootstrap color ]text-color, background & border css starts ======*/\r\n@each $value in $color-bt-name {\r\n    $i: index($color-bt-name, $value);\r\n    .bg-#{$value} {\r\n        background-color: nth($color-bt-color,$i) !important;\r\n    }\r\n    .text-#{$value} {\r\n        color: nth($color-bt-color,$i) !important;\r\n    }\r\n    .b-#{$value} {\r\n        border: 1px solid nth($color-bt-color,$i) !important;\r\n    }\r\n    .text-h-#{$value}:hover {\r\n        color: nth($color-bt-color,$i) !important;\r\n    }\r\n}\r\n/*====== [ overrides bootstrap color ]text-color, background & border css ends ======*/\r\n/*====== border color css starts ======*/\r\n@each $value in $color-bt-name {\r\n    $i: index($color-bt-name, $value);\r\n    .b-#{$value} {\r\n        border: 1px solid nth($color-bt-color,$i);\r\n    }\r\n    .border-bottom-#{$value} td{\r\n        border-bottom: 1px solid nth($color-bt-color,$i);\r\n    }\r\n    .border-bottom-#{$value} th{\r\n        border-bottom: 1px solid nth($color-bt-color,$i) !important;\r\n    }\r\n}\r\n/*====== border color css ends ======*/\r\n\r\n/*====== Card top border css starts ======*/\r\n@each $value in $color-name {\r\n    $i: index($color-name, $value);\r\n    .card-border-#{$value}{\r\n        border-top: 4px solid nth($color-color,$i);\r\n    }\r\n}\r\n/*====== Card top border ends ======*/\r\n/*====== Font-size css starts ======*/\r\n$i: 10;\r\n@while $i<=80 {\r\n    .f-#{$i} {\r\n        font-size: #{$i}px;\r\n    }\r\n    $i: $i+2;\r\n}\r\n/*====== Font-size css ends ======*/\r\n/*====== Font-weight css starts ======*/\r\n$i: 100;\r\n@while $i<=900 {\r\n    .f-w-#{$i} {\r\n        font-weight: #{$i};\r\n    }\r\n    $i: $i+100;\r\n}\r\n/*====== Font-weight css ends ======*/\r\n/*====== text-color, background color css starts ======*/\r\n@each $value in $social-name {\r\n    $i: index($social-name, $value);\r\n    .bg-#{$value} {\r\n        background: nth($social-color,$i);\r\n    }\r\n    .text-#{$value} {\r\n        color: nth($social-color,$i);\r\n    }\r\n}\r\n/*====== text-color css ends ======*/\r\n/*====== width, Height css starts ======*/\r\n$i: 20;\r\n@while $i<=150 {\r\n    .wid-#{$i} {\r\n        width: #{$i}px;\r\n    }\r\n    .hei-#{$i} {\r\n        height: #{$i}px;\r\n    }\r\n    $i: $i+5;\r\n}\r\n.wid-250{\r\n  width: 250px;\r\n}\r\n/*====== width, Height css ends ======*/\r\n/*====== border-width css starts ======*/\r\n$i: 1;\r\n@while $i<=8 {\r\n    .b-wid-#{$i} {\r\n        border-width:#{$i}px;\r\n    }\r\n    $i: $i+1;\r\n}\r\n/*====== border-width css ends ======*/\r\n","@charset \"UTF-8\";\r\n\r\n/*!\r\n * animate.css -http://d