                        }\r\n                    }\r\n\r\n                    &.pcoded-hasmenu {\r\n                        > a {\r\n                            &:after {\r\n                                transition: none;\r\n                                transition-delay: 0;\r\n                            }\r\n                        }\r\n                    }\r\n                }\r\n            }\r\n\r\n            .pcoded-badge {\r\n                transition: none;\r\n            }\r\n\r\n            &:hover {\r\n                .pcoded-badge {\r\n                    transition-delay: 0;\r\n                }\r\n            }\r\n        }\r\n    }\r\n}\r\n/* responsive horizontal menu */\r\n@media only screen and (max-width: 991px) {\r\n    .pcoded-navbar {\r\n        &.theme-horizontal {\r\n            margin-left: 0;\r\n\r\n            ~ .pcoded-header {\r\n                > .collapse:not(.show),\r\n                .container > .collapse:not(.show) {\r\n                    display: inline-flex;\r\n                    background: #fff;\r\n                    box-shadow: 0 1px 20px 0 rgba(69, 90, 100, 0.08);\r\n                }\r\n\r\n                .b-title {\r\n                    color: #fff;\r\n                }\r\n            }\r\n\r\n            .pcoded-inner-navbar {\r\n                .pcoded-hasmenu {\r\n                    &.pcoded-trigger {\r\n                        > .pcoded-submenu {\r\n                            .pcoded-trigger,\r\n                            .pcoded-trigger.edge {\r\n                                > .pcoded-submenu {\r\n                                    position: relative;\r\n                                    left: 0;\r\n                                    min-width: 100%;\r\n                                    margin: 0;\r\n                                    box-shadow: none;\r\n\r\n                                    &:before {\r\n                                        display: none;\r\n                                    }\r\n                                }\r\n                            }\r\n                        }\r\n                    }\r\n                }\r\n            }\r\n        }\r\n    }\r\n}\r\n/* ==========================    Rsponsive Menu  end   ====================== */\r\n/* ******************************************************************************* */\r\n/* * Horizontal */\r\n\r\n.theme-horizontal {\r\n    .sidenav-horizontal-wrapper {\r\n        flex: 1 1 auto;\r\n        width: 0;\r\n\r\n        .sidenav:not(.sidenav-no-animation) & .sidenav-inner {\r\n            transition: margin 0.2s;\r\n        }\r\n    }\r\n\r\n    .sidenav-horizontal-next,\r\n    .sidenav-horizontal-prev {\r\n        position: relative;\r\n        display: block;\r\n        flex: 0 0 auto;\r\n        width: 2.25rem;\r\n        background-color: $menu-dark-background;\r\n        z-index: 9;\r\n\r\n        &::after {\r\n            content: \"\";\r\n            position: absolute;\r\n            top: 50%;\r\n            left: 50%;\r\n            display: block;\r\n            width: 0.8rem;\r\n            height: 0.8rem;\r\n            border: 2px solid;\r\n            border-top: 0;\r\n        }\r\n\r\n        &.disabled {\r\n            cursor: default !important;\r\n            opacity: 0;\r\n        }\r\n    }\r\n\r\n    .sidenav-horizontal-prev::after {\r\n        border-right: 0;\r\n        transform: translate(-50%, -50%) rotate(45deg);\r\n    }\r\n\r\n    .sidenav-horizontal-next::after {\r\n        border-left: 0;\r\n        transform: translate(-50%, -50%) rotate(-45deg);\r\n    }\r\n\r\n    .sidenav-horizontal {\r\n        &:after,\r\n        &:before {\r\n            content: \"\";\r\n            background: $menu-dark-background;\r\n            width: 100%;\r\n            position: absolute;\r\n            top: 0;\r\n            height: 100%;\r\n            z-index: 5;\r\n            @media only screen and (max-width: 991px) {\r\n                display: none;\r\n            }\r\n        }\r\n\r\n        &:before {\r\n            left: 100%;\r\n        }\r\n\r\n        &:after {\r\n            right: 100%;\r\n        }\r\n    }\r\n\r\n    &.menu-light {\r\n        .sidenav-horizontal {\r\n            &:after,\r\n            &:before {\r\n                background: $menu-light-background;\r\n            }\r\n        }\r\n    }\r\n}\r\n@keyframes sidenavDropdownShow {\r\n    0% {\r\n        opacity: 0;\r\n        transform: translateY(-.5rem);\r\n    }\r\n\r\n    100% {\r\n        opacity: 1;\r\n        transform: translateY(0);\r\n    }\r\n}\r\n\r\n.pcoded-navbar {\r\n    .card {\r\n        background: rgba(255,255,255,0.1);\r\n        margin: 20px;\r\n        box-shadow: 0 0 0 1px transparentize($theme-border,.85);\r\n        color: rgba(255, 255, 255, 0.6);\r\n\r\n        h6 {\r\n            color: rgba(255, 255, 255, 0.6);\r\n        }\r\n\r\n        .close {\r\n            position: absolute;\r\n            color: $menu-dark-text-color;\r\n            position: absolute;\r\n            top: 10px;\r\n            right: 12px;\r\n            opacity: 0.8;\r\n            text-shadow: none;\r\n        }\r\n    }\r\n\r\n    &.menu-light {\r\n        .card {\r\n            background: rgba(0,0,0,0.1);\r\n            box-shadow: 0 0 0 1px $theme-background;\r\n            color: rgba(0, 0, 0, 0.6);\r\n\r\n            h6 {\r\n                color: $menu-light-text-color;\r\n            }\r\n\r\n            .close {\r\n                color: $menu-light-text-color;\r\n            }\r\n        }\r\n    }\r\n\r\n    &.navbar-collapsed {\r\n        .card {\r\n            display: none;\r\n        }\r\n    }\r\n}\r\n// ==========================    Menu Styler Start     ======================\r\n$style-block-width: 400px;\r\n\r\n.menu-styler {\r\n    z-index: 1029;\r\n    position: relative;\r\n    color: $theme-font-color;\r\n\r\n    h5,\r\n    h6 {\r\n        color: $theme-heading-color;\r\n    }\r\n\r\n    hr {\r\n        border-top: 1px solid rgba(0,0,0,.1);\r\n    }\r\n\r\n    .style-toggler {\r\n        > a {\r\n            position: fixed;\r\n            right: 0;\r\n            top: 80px;\r\n            z-index: 1031;\r\n            transition: 0.3s ease-in-out;\r\n\r\n            &:before {\r\n                content: \"\";\r\n                width: 45px;\r\n                height: 45px;\r\n                position: absolute;\r\n                right: 0;\r\n                top: 0;\r\n                z-index: 1001;\r\n                background: $danger-color;\r\n                transition: 0.3s ease-in-out;\r\n                border-radius: 30px 0 0 30px;\r\n                box-shadow: -4px 0 8px transparentize($danger-color,0.84), -6px 0 8px rgba(0, 0, 0, 0.1);\r\n            }\r\n\r\n            &:after {\r\n                content: \"\\e849\";\r\n                top: 12px;\r\n                font-size: 21px;\r\n                position: absolute;\r\n                right: 11px;\r\n                font-family: \"feather\";\r\n                z-index: 1029;\r\n                line-height: 1;\r\n                color: #fff;\r\n                transition: 0.3s ease-in-out;\r\n                @media all and (-ms-high-contrast:none) {\r\n                    line-height: 0.5;\r\n                    top: 18px;\r\n                }\r\n            }\r\n        }\r\n    }\r\n\r\n    &.open,\r\n    &.prebuild-open {\r\n        .style-toggler {\r\n            > a {\r\n                right: $style-block-width;\r\n                animation: sh-shake 0s ease infinite;\r\n\r\n                &:after {\r\n                    content: \"\\e847\";\r\n                    animation: anim-rotate 0s linear infinite;\r\n                }\r\n            }\r\n        }\r\n\r\n        .prebuild-toggler > .prebuild-group {\r\n            right: calc(#{$style-block-width} - 100px);\r\n        }\r\n    }\r\n\r\n    &.open {\r\n        .style-block {\r\n            right: 0;\r\n            box-shadow: -6px 0 12p