der-color: $primary-color;\r\n            }\r\n        }\r\n    }\r\n\r\n    li > {\r\n        a,\r\n        span {\r\n            color: #222;\r\n        }\r\n    }\r\n}\r\n\r\n.footable-details.table,\r\n.footable.table {\r\n    margin-bottom: 0;\r\n}\r\n\r\ntable {\r\n    &.footable > tfoot > tr.footable-paging > td > span.label {\r\n        margin-bottom: 0;\r\n    }\r\n\r\n    &.footable-paging-center > tfoot > tr.footable-paging > td {\r\n        padding-bottom: 0;\r\n    }\r\n}\r\n\r\n.table-columned > {\r\n    tbody > {\r\n        tr > {\r\n            td {\r\n                border: 0;\r\n                border-left: 1px solid $theme-border;\r\n            }\r\n\r\n            th {\r\n                border: 0;\r\n            }\r\n        }\r\n    }\r\n}\r\n/**====== Foo-table css end ======**/\r\n/**======= basic table css start =======**/\r\n.table.table-xl {\r\n    td,\r\n    th {\r\n        padding: 1.25rem 2rem;\r\n    }\r\n}\r\n\r\n.table.table-lg {\r\n    td,\r\n    th {\r\n        padding: 0.9rem 2rem;\r\n    }\r\n}\r\n\r\n.table.table-de {\r\n    td,\r\n    th {\r\n        padding: 0.75rem 2rem;\r\n    }\r\n}\r\n\r\n.table.table-sm {\r\n    td,\r\n    th {\r\n        padding: 0.6rem 2rem;\r\n    }\r\n}\r\n\r\n.table.table-xs {\r\n    td,\r\n    th {\r\n        padding: 0.4rem 2rem;\r\n    }\r\n}\r\n/**======= basic table css end ======**/\r\n\r\n#row-delete,\r\n#row-select {\r\n    .selected {\r\n        background-color: $primary-color;\r\n        color: #fff;\r\n    }\r\n}\r\n\r\ntable {\r\n    &.dataTable {\r\n        tbody {\r\n            > tr {\r\n                &.selected,\r\n                > .selected {\r\n                    background: $primary-color;\r\n\r\n                    table.dataTable tbody > tr > .selected td,\r\n                    table.dataTable tbody > tr.selected,\r\n                    th {\r\n                        &.select-checkbox {\r\n                            &:after {\r\n                                text-shad