oday-button {\r\n        display: none;\r\n    }\r\n}\r\n","$ico-col: #4680ff,#6610f2,#6f42c1,#e83e8c,#ff5252,#fd7e14,#ffba57,#9ccc65,#20c997,#00acc1; /**  =====================\n      Icon layouts css start\n==========================  **/\r\n\r\n.i-main {\r\n    .i-block {\r\n        display: inline-flex;\r\n        align-items: center;\r\n        justify-content: center;\r\n        width: 70px;\r\n        height: 70px;\r\n        margin: 5px;\r\n        border: 1px solid #f1f1f1;\r\n        position: relative;\r\n        cursor: pointer;\r\n\r\n        i {\r\n            font-size: 30px;\r\n        }\r\n\r\n        label {\r\n            margin-bottom: 0;\r\n            display: none;\r\n        }\r\n\r\n        span.ic-badge {\r\n            position: absolute;\r\n            bottom: 0;\r\n            right: 0;\r\n        }\r\n\r\n        .flag-icon-background {\r\n            width: 40px;\r\n            height: 40px;\r\n        }\r\n    }\r\n}\r\n\r\n.sample-icon {\r\n    .i-block {\r\n        display: inline-flex;\r\n        align-items: center;\r\n        justify-content: center;\r\n        width: 70px;\r\n        height: 70px;\r\n        margin: 5px;\r\n        border-radius: 50%;\r\n        border: 1px solid #f1f1f1;\r\n        position: relative;\r\n        cursor: pointer;\r\n\r\n        i {\r\n            font-size: 28px;\r\n        }\r\n        // $ico-col\r\n        $i: 1;\r\n        $t: 1;\r\n        @while $t < 5 {\r\n            @each $value in $ico-col {\r\n                &:nth-child(#{$i}) {\r\n                    border-color: $value;\r\n                    background: transparentize($value, 0.9);\r\n\r\n                    i {\r\n                        color: $value;\r\n                    }\r\n                    $i: $i+1;\r\n                }\r\n            }\r\n            $t: $t+1;\r\n        }\r\n    }\r\n}\r\n/**====== Icon layouts css end ======**/\r\n","/**  =====================\n      user card css start\n==========================  **/\r\n// [ user card widget ] start\r\n\r\n.user-card {\r\n    .cover-img-block {\r\n        position: relative;\r\n\r\n        .change-cover {\r\n            position: absolute;\r\n            top: 30px;\r\n            left: 30px;\r\n            z-index: 5;\r\n            opacity: 0;\r\n            transition: all 0.3s ease-in-out;\r\n\r\n            .dropdown-toggle {\r\n                color: #fff;\r\n                background: rgba(0,0,0,0.6);\r\n                width: 45px;\r\n                height: 45px;\r\n                border-radius: 50%;\r\n                font-size: 20px;\r\n                display: flex;\r\n                align-items: center;\r\n                justify-content: center;\r\n\r\n                &:after {\r\n                    display: none;\r\n                }\r\n            }\r\n        }\r\n\r\n        .overlay {\r\n            position: absolute;\r\n            top: 0;\r\n            left: 0;\r\n            width: 100%;\r\n            height: 100%;\r\n            opacity: 0;\r\n            z-index: 1;\r\n            background: rgba(0,0,0,0.4);\r\n            transition: all 0.3s ease-in-out;\r\n        }\r\n\r\n        &:hover {\r\n            .change-cover,\r\n            .overlay {\r\n                opacity: 1;\r\n            }\r\n        }\r\n    }\r\n\r\n    .user-about-block {\r\n        margin-top: -30px;\r\n\r\n        img {\r\n            box-shadow: 0 0 0 5px #fff;\r\n        }\r\n\r\n        .certificated-badge {\r\n            position: absolute;\r\n            bottom: -5px;\r\n            right: -5px;\r\n            border-radius: 50%;\r\n            width: 30px;\r\n            height: 30px;\r\n            background: #fff;\r\n            padding: 5px 3px;\r\n\r\n            .bg-icon {\r\n                font-size: 22px;\r\n            }\r\n\r\n            .front-icon {\r\n                font-size: 11px;\r\n                position: absolute;\r\n                top: 11px;\r\n                left: 9px;\r\n            }\r\n        }\r\n    }\r\n\r\n    .hover-data {\r\n        position: absolute;\r\n        top: 0;\r\n        left: 0;\r\n        width: 100%;\r\n        height: 100%;\r\n        background: rgba(36, 46, 62, 0.92);\r\n        text-align: center;\r\n        display: flex;\r\n        align-items: center;\r\n        justify-content: center;\r\n        transform: scale(0);\r\n        transition: all 0.3s ease-in-out;\r\n    }\r\n\r\n    &:hover {\r\n        .hover-data {\r\n            transform: scale(1);\r\n        }\r\n    }\r\n\r\n    .change-profile {\r\n        .dropdown-toggle::after {\r\n            display: none;\r\n        }\r\n\r\n        .profile-dp {\r\n            position: relative;\r\n            overflow: hidden;\r\n            padding: 5px;\r\n            width: 110px;\r\n            height: 110px;\r\n            border-radius: 50%;\r\n\r\n            .overlay {\r\n                position: absolute;\r\n                top: 5px;\r\n                left: 5px;\r\n                width: calc(100% - 10px);\r\n                height: calc(100% - 10px);\r\n                border-radius: 50%;\r\n                opacity: 0;\r\n                z-index: 1;\r\n                overflow: hidden;\r\n                background: rgba(0,0,0,0.4);\r\n                transition: all 0.3s ease-in-out;\r\n\r\n                span {\r\n                    background: rgba(0, 0, 0, 0.5);\r\n                    color: #fff;\r\n                    position: absolute;\r\n                    bottom: 0;\r\n                    width: 100%;\r\n                    text-align: center;\r\n                    border-top: 1px solid rgba(255, 255, 255, 0.4);\r\n                    padding: 0 0 5px;\r\n                }\r\n            }\r\n\r\n            &:hover {\r\n                .overlay {\r\n                    opacity: 1;\r\n                }\r\n            }\r\n        }\r\n    }\r\n    @media only screen and (max-width: 992px) {\r\n        .user-about-block {\r\n            margin-top: -10px;\r\n        }\r\n\r\n        .wid-80 {\r\n            width: 60px;\r\n        }\r\n\r\n        .change-profile .profile-dp {\r\n            width: 90px;\r\n            height: 90px;\r\n        }\r\n\r\n        &.user-card-2.shape-right .cover-img-block {\r\n            clip-path: none;\r\n        }\r\n\r\n        .cover-img-block {\r\n            .change-cover {\r\n                .dropdown-toggle {\r\n                    padding: 5px 7px;\r\n                }\r\n            }\r\n        }\r\n    }\r\n}\r\n\r\n.user-card-2 {\r\n    .cover-img-block {\r\n        clip-path: polygon(0 0, 100% 0%, 100% 100%, 0% 80%);\r\n    }\r\n\r\n    &.shape-right {\r\n        .cover-img-block {\r\n            clip-path: polygon(0 0, 100% 0%, 100% 80%, 0% 100%);\r\n        }\r\n    }\r\n\r\n    &.shape-center {\r\n        .cover-img-block {\r\n            clip-path: polygon(0 0, 100% 0%, 100% 80%, 50% 100%, 0% 80%);\r\n        }\r\n    }\r\n}\r\n\r\n.user-card-3 {\r\n    .certificated-badge {\r\n        position: absolute;\r\n        bottom: 5px;\r\n        right: 5px;\r\n        border-radius: 50%;\r\n        width: 40px;\r\n        height: 40px;\r\n        background: #fff;\r\n        padding: 5px 3px;\r\n\r\n        .bg-icon {\r\n            font-size: 30px;\r\n        }\r\n\r\n        .front-icon {\r\n            font-size: 14px;\r\n            position: absolute;\r\n            top: 14px;\r\n            left: 13px;\r\n        }\r\n    }\r\n\r\n    .social-top-link {\r\n        position: absolute;\r\n        top: 20px;\r\n        left: 20px;\r\n    }\r\n\r\n    &.social-hover {\r\n        .social-top-link {\r\n            top: 20px;\r\n            left: 20px;\r\n            overflow: hidden;\r\n\r\n            .btn,\r\n            li {\r\n                margin-left: -100px;\r\n                transition: all 0.2s ease-in-out;\r\n                $i: 1;\r\n                @while $i<=10 {\r\n                    &:nth-child(#{$i}) {\r\n                        transition-delay: $i*0.05s;\r\n                    }\r\n                    $i: $i+1;\r\n                }\r\n            }\r\n        }\r\n\r\n        &:hover {\r\n            .social-top-link {\r\n                .btn,\r\n                li {\r\n                    margin-left: 0;\r\n                }\r\n            }\r\n        }\r\n    }\r\n}\r\n// [ user card widget ] end\r\n// [ user profile page ] start\r\n\r\n.user-profile {\r\n    margin-top: -25px;\r\n    margin-left: -25px;\r\n    margin-right: -25px;\r\n    background: #fff;\r\n\r\n    .card-body {\r\n        position: relative;\r\n        z-index: 1;\r\n        box-shadow: 0 2px 4px -2px rgba(54, 80, 138, 0.3);\r\n\r\n        .overlay {\r\n            > span {\r\n                cursor: pointer;\r\n            }\r\n        }\r\n    }\r\n\r\n    .cover-img-block {\r\n        &:hover {\r\n            > img {\r\n                filter: grayscale(1) blur(1px);\r\n            }\r\n        }\r\n    }\r\n\r\n    .profile-dp {\r\n        .overlay {\r\n            text-align: left;\r\n        }\r\n    }\r\n\r\n    .user-about-block .certificated-badge {\r\n        bottom: 8px;\r\n        right: 0;\r\n        z-index: 2;\r\n    }\r\n\r\n    .profile-tabs {\r\n        border-bottom: 0;\r\n        margin-top: 30px;\r\n\r\n        .nav-item {\r\n            margin-bottom: -0px;\r\n\r\n            .nav-link {\r\n                position: relative;\r\n\r\n                &:after {\r\n                    content: \"\";\r\n                    background: $primary-color;\r\n                    position: absolute;\r\n                    transition: all 0.3s ease-in-out;\r\n                    left: 50%;\r\n                    right: 50%;\r\n                    bottom: -1px;\r\n                    height: 2px;\r\n                    border-radius: 2px 2px 0 0;\r\n                }\r\n            }\r\n        }\r\n\r\n        .nav-link:hover {\r\n            border-color: transparent;\r\n        }\r\n\r\n        .nav-item.show .nav-link,\r\n        .nav-link.active {\r\n            border-color: transparent;\r\n\r\n            &:after {\r\n                left: 0;\r\n                right: 0;\r\n            }\r\n        }\r\n    }\r\n    @media only screen and (max-width: 575px) {\r\n        margin-top: -30px;\r\n        margin-left: -15px;\r\n        margin-right: -15px;\r\n    }\r\n}\r\n// [ user profile page ] end\r\n// [ user list page ] start\r\n\r\n.user-profile-list {\r\n    table {\r\n        border-spacing: 0 10px;\r\n        width: calc(100% - 10px);\r\n        margin: 0 5px;\r\n\r\n        tbody {\r\n            tr {\r\n                box-shadow: $card-shadow;\r\n                border-radius: $border-radius;\r\n                position: relative;\r\n\r\n                .overlay-edit {\r\n                    position: absolute;\r\n                    opacity: 0;\r\n                    top: 0;\r\n                    right: 0;\r\n                    background: $primary-color;\r\n                    bottom: 0;\r\n                    left: 0;\r\n                    display: flex;\r\n                    align-items: center;\r\n                    justify-content: center;\r\n\r\n                    .btn {\r\n                        border-radius: 50%;\r\n                        margin: 0 3px;\r\n                        width: 35px;\r\n                        height: 35px;\r\n                        display: inline-flex;\r\n                        align-items: center;\r\n                        justify-content: center;\r\n                    }\r\n                }\r\n\r\n                &:hover {\r\n                    box-shadow: 0 0 6px 0 $primary-color;\r\n\r\n                    td {\r\n                        cursor: pointer;\r\n                        color: #fff;\r\n                        background: $primary-color;\r\n\r\n                        h6 {\r\n                            color: #fff;\r\n                        }\r\n\r\n                        .overlay-edit {\r\n       